*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1 {
    font-size: calc(16px + 3.5vw);
    text-align: center;
    margin-top: 9vw;
    padding-bottom: 1vw;
}

.profile-holder {
    margin: 1rem;
    display: flex;
    padding: 12%;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
}

.profile-pic {
    object-fit: cover;
    position: absolute;
    height: auto;
    max-height: 30rem;
    margin-left: auto;
    margin-right: auto;
}

.profile-heading {
    object-fit: cover;
    position: absolute;
    margin-top: 10%;
}